@import "../../variables.scss";

.footer {
    // height: 400px;
    padding-top: 50px;
    padding-bottom: 50px;
    background: linear-gradient(180deg, $dark-bg 95%, $juicy-green 100%);

    & svg {
        font-size: 25px;
        color: $juicy-green;

        &:hover {
            color: $juicy-orange;
        }
    }

    &__content {
        width: 1170px;
        margin: 0 auto;
        display: flex;
        justify-content: space-evenly;
    }

    &__company {
        width: 30%;
        color: #fff;
        text-align: justify;

        &-logo {
            width: 100px;
        }

        &-fb {
            font-size: 30px;
            color: $juicy-green;

            &:hover {
                color: $juicy-orange;
            }
        }
    }


    &__contacts {
        width: 30%;
        color: #fff;
    }

    &__email a{
        padding-left: 20px;
    }

    &__phones{
        display: flex;
    }

    &__phones svg{
        transform: rotate(-45deg);
    }
}